body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input {
  margin-bottom: 10px;
}

button {
  color: #6c757d;
  background-color: transparent;
  border-radius: 4px;
  border-color: #6c757d;
}

button:hover,
button:active {
  color: #fff;
  background-color: #6c757d;
}

.persons-list {
  margin-bottom: 30px;
  max-width: 500px;
}

.persons-list li {
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #dcdcdc;
}

.persons-list li button {
  float: right;
}

.person .name {
  font-weight: bold;
}

.person .number {
  font-style: italic;
  color: #999;
}

/*Alert styles*/

.alert {
  position: relative;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid transparent;
  border-radius: 4px;
}

.success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
}

.error {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
}
